<template>
<el-main>
    <NewTodo
    @updateFormChild="addNewTodo"
    :newTodo="newTodo"></NewTodo>
    <SingleTodo 
    v-for="(i , index) in load.Data"  
    :title="i.Title"
    :Done="i.Done"
    :time="i.StartTime"
    :key="index"
    :index="index"
    @FixByChild = "Fix"
    @finishedByChild="finished"
    @RemoveByChild="Remove">
    </SingleTodo>
    <FixTodo
    v-on:closeByChild="closeByChild"
    :dialogFormVisible="dialogFormVisible"
    :form='fixForm'
    ></FixTodo>
</el-main>
</template>

<script>
import FixTodo from "../components/Todo/FixTodo"
import SingleTodo from "../components/Todo/SingleTodo"
import NewTodo from "../components/Todo/NewTodo"
import {AxiosGet , TodoAdd ,AxiosDelete ,TodoPut} from "../../util/submit"
export default {
components:{
    SingleTodo,
    NewTodo,
    FixTodo
},
data(){
    return{
        dialogFormVisible:false,
        load : {},
        iteminpage:0,
        newTodo:{
                Content: "",
                Title: "",
                StartTime: ''
        },
        Username: "",
        fixForm:{},
        }
},
computed:{
        name() {
            return this.$store.state.UserName
        }
    },
methods:{
    closeByChild(value){
            this.dialogFormVisible = value
    },
    allTodo(_this , name ){
        let allTodo =   AxiosGet("todolist" ,name)
        allTodo.then((responseObj)=>{
            _this.load = responseObj.data
            for(var i in _this.load.Data) _this.iteminpage++//eslint-disable-line no-unused-vars
        }).catch((err)=>{
            console.log(err)
            this.$message.error('Error!')
        })
    },
    finished(key){
        
        let payload = this.load.Data[key].TodoId
        let finished = TodoPut(payload)
        finished.then(()=>{
            this.$message("Finished")
        }).catch(()=>{

        })
    },
    addNewTodo(newTodo){
        newTodo.username = this.name
        let addTodo = TodoAdd('todolist',newTodo)
        addTodo.then(()=>{
            this.load.Data.push(newTodo)
            this.newTodo = {
            Title: "",
            }
        }).catch(()=>{
            this.$message.error('Error!')
        })
        
    },
    Remove(key){
        console.log(key)
        let deleteTodo =  AxiosDelete( "todolist/",this.load.Data[key].TodoId)
        deleteTodo.then((response)=>{
            console.log(response)
            this.load.Data.splice(key, 1)
        }).catch((err)=>{
            console.log(err)
        })
    },
    Fix(key){
        console.log(key)
        this.fixForm = this.load.Data[key]
        this.dialogFormVisible = true;
        
    }
},
mounted(){
    let _this = this
    let name = sessionStorage.getItem("UserName")
    this.allTodo(_this,name)
    
}


}
</script>

