<template>
<el-main>
    <el-card>
        <el-form ref="form"  label-width="50px">
        <el-form-item label="标题">
            <el-input 
            :autosize="autosize" 
            v-model="newTodo.Title"></el-input>
        </el-form-item>
        
        <el-form-item>
            <el-button 
            type="primary"  
            @click="UpdateTodo">update</el-button>
        </el-form-item>
        </el-form>
    </el-card>
</el-main>
</template>

<script>
export default {
props:{
    newTodo:{type:Object,required:true}
},
data(){
    return{
        autosize:true,
        
    }
},
methods:{
    UpdateTodo(){
        this.newTodo.StartTime = new Date().format("YYYY-MM-DD hh:mm:ss");
        this.$emit('updateFormChild' , this.newTodo)
        
    }
}

}
</script>

