<template>
<el-main>
   <el-card :class="{finished : Done}">
      <div slot="header" class="clearfix">
        <span :time="time">Deadline：{{time}}</span>
        <el-button 
        style="float: right;" 
        type="info"
        plain 
        :disabled='Done'
        @click="finished">Finished!</el-button>
        <el-button 
        style="float: right; " 
        type="info" 
        plain 
        @click="Remove">Remove</el-button>
        <el-button 
        style="float: right; " 
        span
        v-if="!Done"
        @click="Fix">Fix</el-button>
      </div>
      <div :title="title">
        {{title}}
      </div>
  </el-card>
</el-main>
 
</template>

<script>
import {myget ,} from "../../../util/submit"// eslint-disable-line no-unused-vars
export default {
  props: {
    content:{type:String},
    title:{type:String,require:true},
    Done:{type:Boolean},
    time:{type:String},
    index:{type:Number,require:true},
  },
  methods: {
    finished(){
      this.Done = true;
      this.$emit('finishedByChild' , this.index)
    },
    Remove(){
      this.$emit('RemoveByChild' , this.index)
    },
    Fix(){
        this.$emit('FixByChild',this.index)
      }
  }

}
</script>

<style scoped>
.text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
  .finished{
    background-color: #909399;
  }
</style>